import { h } from "@stencil/core";
export class RaulComplexGrid {
    render() {
        return (h("slot", null));
    }
    static get is() { return "raul-complex-grid"; }
    static get originalStyleUrls() { return {
        "$": ["raul-complex-grid.scss"]
    }; }
    static get styleUrls() { return {
        "$": ["raul-complex-grid.css"]
    }; }
}
